/*
  From the old squarespace page
*/

.scroll-arrow {
/*display:none;*/
cursor: pointer;
position: absolute;
bottom: 0;
left: 50%;
margin-left: -75px;
width: 150px;
z-index: 10;
height: 47px;
text-align: center;
padding: 0;
-webkit-box-sizing: border-box;
-moz-box-sizing: border-box;
box-sizing: border-box;

span {
  display: none;
  letter-spacing: 2px;
  font-size: 12px;
  text-align: center;
}

&:hover span {
  display: block;
}

&:before {
text-align: center;
cursor: pointer;
width: 47px;
height: 47px;
line-height: 47px;
font: normal normal normal 47px/1 FontAwesome;
-webkit-font-smoothing: antialiased;
content: "\f107";
display: inline-block;
vertical-align: middle;
  }

  &:hover::before {
    display:none;
  }

}
